section {
    box-sizing: border-box;
    padding: 10px;
    margin-top: 10%;
    width: 100%;
    min-height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: left;
    background-color: #8b8b8b;
    opacity: 95%;
  }
  section .content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: left;
    background-color: #8b8b8b;
    opacity: 95%;
    margin: 10;
    padding: 0.5em;
    border: 10px solid #ccc;
    width: 100%;
    word-wrap: break-word;
  }
  section .content pre {
    margin: 5px;
  }
  section .content .body {
    outline: none;
    resize: none;
    font-family: monospace;
    transition: background 0.25s, color 0.25s;
  }
  section .content .body:focus {
    background: #4a4a4a;
    color: #eeeeee;
  }
  section .content .body:hover {
    background: #4a4a4a;
    color: #eeeeee;
  }
  section .loader {
    color: #ffffff;
    font-size: 30px;
    text-indent: -9999em;
    overflow: hidden;
    width: 1em;
    height: 1em;
    border-radius: 50%;
    margin: 72px auto;
    position: relative;
    transform: translateZ(0);
    -webkit-animation: load6 1.7s infinite ease, round 1.7s infinite ease;
    animation: load6 1.7s infinite ease, round 1.7s infinite ease;
  }
